import React, {Component} from "react";
import {findDOMNode} from 'react-dom';
import PropTypes from "prop-types";
//import WithClipboard from "components/Clipboard";
import Moment from 'react-moment';


const ListItemTweet = ({item}) => {
    return (
    <li key={item.id} className="tweet">
       <p>
           <img className="twitter" alt="" src="Twitter_Logo_Blue.png" />
           <img className="avatar" alt="" src={item.img} />
           <a href={item.url}> 
               <span>{item.name}</span>
               <span> (for <Moment locale="da" fromNow ago>{item.time}</Moment> siden) </span><br/>
           </a>
           <span>@{item.user}</span>
       </p>
       <p><span dangerouslySetInnerHTML={{__html:item.html}} /></p>
    </li>
 )}


const ListItemTweetWithContext = ({item}) => {
    return (
    <li key={item.id} className="tweet">
       <p>
           <img className="avatar" alt="" src={item.context_img} />
           <a href={item.context_url}>
               <span>{item.context_name}</span>
               <span> (for <Moment locale="da" fromNow ago>{item.context_time}</Moment> siden): </span><br/>
           </a>
           <span dangerouslySetInnerHTML={{__html: item.context_html}} />
       </p>
       <p>
           <img className="twitter" alt="" src="Twitter_Logo_Blue.png" />
           <img className="avatar" alt="" src={item.img} />
           <a href={item.url}>
               <span>{item.name}</span>
               <span> (for <Moment locale="da" fromNow ago>{item.time}</Moment> siden) </span><br/>
           </a>
           <span>@{item.user}</span>
       </p>
       <p><span dangerouslySetInnerHTML={{__html: item.html}} /></p>
    </li>
)}

class ListItemArticle extends Component {
    componentDidMount(){
        if (window.location.pathname.indexOf(this.props.item.slug) !== -1){
            var node = findDOMNode(this.refs.listitem)
            console.log("slug is mine", node);
            this.props.setHashedNode(node);
        }
    }
    render(){
    return(
    <li ref="listitem" key={this.props.item.anchor} className="article" id={this.props.item.anchor}>
       <p className="avatar">
           <img className="avatar" alt="" src={"/img/danx.png"} />
           <a href={"/article/"+ this.props.item.slug }>
               <span>{"Dan X"}</span>
               <span> (for <Moment locale="da" fromNow ago>{this.props.item.time}</Moment> siden) </span><br/>
           </a>
       </p>
       {this.props.item.svg && 
           <p className="image">
               <img className="image" alt={this.props.item.heading} src={"/img/" + this.props.item.slug + ".svg"}/>
           </p>
       }
       <p>
           <h4>{this.props.item.heading}</h4>
           <span dangerouslySetInnerHTML={{__html: this.props.item.html}} />
       </p>
    </li>
  )}
}

const ListItemImage = ({item}) => {
    return(
    <li key={item.id} className="article">
       <p>
           <img className="avatar" alt="" src="danx.png" />
           <h4>{item.heading}</h4>
       </p>
       <p><span dangerouslySetInnerHTML={{__html: item.html}} /></p>
    </li>
)}

const SuggestionSectionList = ({ setHashedNode, sectionName, items, onMoreClick }) => {
  const listItems = items.map(item => ({
    "tweet":(
      <ListItemTweet item={item} />
    ),
    "tweet-with-context":(
      <ListItemTweetWithContext item={item} />
    ),
    "article":(
      <ListItemArticle setHashedNode={setHashedNode} item={item} />
    ),
    "image":(
      <ListItemImage item={item} />
    )}[item.type])
  );
  return ( 
    <ul>
      <li className="image">
        <p>
          <img className="" alt="" src={"/img/"+sectionName.replace(/ /g,"-") + ".svg"} />
        </p>
       </li>
      {listItems}
    </ul>
   )
};

SuggestionSectionList.propTypes = {
  setHashedNode: PropTypes.func,
  sectionName: PropTypes.string,
  items: PropTypes.arrayOf(PropTypes.object).isRequired,
  onMoreClick: PropTypes.func
};

const SuggestionSection = ({ setHashedNode, sectionName, items }) => {
  return (
    <section>
      <div>
        <h3>{sectionName}</h3>
        <SuggestionSectionList setHashedNode={setHashedNode} sectionName={sectionName} items={items} />
      </div>
    </section>
  );
};

SuggestionSection.propTypes = {
  setHashedNode: PropTypes.func,
  sectionName: PropTypes.string,
  items: PropTypes.arrayOf(PropTypes.object).isRequired,
  onMoreClick: PropTypes.func
};

export { SuggestionSectionList, SuggestionSection };



