export default function groupBy(arr, key) {
   var themap = arr.reduce((sum, item) => {
    const groupByVal = item[key];
    let groupedItems = sum.get(groupByVal) || [];
    groupedItems.push(item);
    return sum.set(groupByVal, groupedItems);
  }, new Map());
  var sarr = Array.from(themap);
  sarr.sort();
  // now remove the cheat sorter from the group keys
  sarr.forEach((item, index, array) => {
    item[0] = item[0].split(" "); 
    item[0].shift()
    item[0] = item[0].join(" ");
    // now sort the items in the group reversed according to time
    item[1].sort((a, b) => {
      if (a.time < b.time) { return 1; }
      if (a.time > b.time) { return -1; }
      return 0;
    })
  })
  return sarr; 
}



