import React, {Component} from "react";
import PropTypes from "prop-types";
import Masonry from "react-masonry-component";
import { SuggestionSection } from "./SuggestionSection";
import "./SearchSuggestionsGrid.css";

class SearchSuggestionsGrid extends Component {
  constructor(props){
      super(props);
      this.state={
          hashedNode: null
      };
  }
  scrollToHash(laidOutItems){
    if(this.state.hashedNode){
        this.state.hashedNode.scrollIntoView();
    }
    console.log("scrolling to node "+ this.state.hashedNode)
  }
  setHashedNode(node){
    this.setState({hashedNode:node})
  }
  render(){
    const suggestionItems = [...this.props.suggestions].map(([key, value]) => (
      <SuggestionSection setHashedNode={this.setHashedNode.bind(this)} key={key} sectionName={key} items={value} />
    ));

    return (
      <Masonry
        key={new Date()}
        onLayoutComplete = {laidOutItems=> this.scrollToHash(laidOutItems)}
        className={"search-suggestions-grid"}
        elementType={"div"}
        options={{}}
        disableImagesLoaded={false}
        updateOnEachImageLoad={false}
      >
        {suggestionItems}
      </Masonry>
    );
  }
};

SearchSuggestionsGrid.propTypes = {
  suggestions: PropTypes.array,
  onMoreClick: PropTypes.func
};

export default SearchSuggestionsGrid;



